import React, { Suspense } from "react";
import { Redirect, Route, Switch, withRouter } from "react-router";

import Layout from "./hoc/Layout/Layout";
import Spinner from "./components/UI/Spinner/Spinner";
import LandingPage from "./containers/content/LandingPage/LandingPage";
import AboutPage from "./containers/content/AboutPage/AboutPage";

import ContactPage from "./containers/content/ContactPage/ContactPage";

import WorkPage from "./containers/content/WorkPage/WorkPage";
const App = () => (
  <Layout>
    <Suspense fallback={<Spinner />}>
      <Switch>
        <Route path="/experience" component={WorkPage} />
        <Route path="/about" component={AboutPage} />
        <Route path="/contact" component={ContactPage} />
        <Route exact path="/" component={LandingPage} />
        <Redirect to="/" />
      </Switch>
    </Suspense>
  </Layout>
);

export default withRouter(App);
